'use strict';

var React = require('react'),
    Reflux = require('reflux'),
    userActions = require('../../actions/userActions'),
    uiActions = require('../../actions/uiActions'),
    Router = require('react-router'),
    $__0=    Router,Link=$__0.Link;


var NavBar = React.createClass({displayName: "NavBar",

  componentDidMount: function() {
    var pathString = this.props.path.toLowerCase().trim();
    if (pathString === String('/login')) {
      uiActions.showOverlay('login');
    } else if (pathString === String('/register')) {
      uiActions.showOverlay('register');
    }
  },

  render: function() {
    var user = this.props.user;
    var navLinks = user.loggedIn ? (
      /* jshint ignore:start */
      React.createElement("div", {className: "nav-list float-right"}, 
        React.createElement("span", {className: "nav-item"}, 
          "Hello ", React.createElement(Link, {to: "user"}, user.firstName ? user.firstName : user.email)
        ), 
        React.createElement("span", {className: "nav-item"}, 
          React.createElement(Link, {to: "settings"}, "My Account")
        ), 
        React.createElement("span", {className: "nav-item"}, 
          React.createElement(Link, {to: "/logout", onClick: this.handleLogout}, "Logout")
        )
      )
      /* jshint ignore:end */
    ) : (
      /* jshint ignore:start */
      React.createElement("div", {className: "nav-list float-right"}, 
        React.createElement("span", {className: "nav-item"}, 
          React.createElement("a", {onClick:  uiActions.showOverlay.bind(this,'login') }, "Sign In")
        ), 
        React.createElement("span", {className: "nav-item"}, 
          React.createElement("a", {onClick:  uiActions.showOverlay.bind(this,'register') }, "Register")
        )
      )
      /* jshint ignore:end */
    );

    return (
      /* jshint ignore:start */
      React.createElement("div", {className: "navbar"}, 
        React.createElement("div", {className: "nav-list float-left"}, 
          React.createElement("div", {className: "menu-title"}, React.createElement(Link, {to: "app"}, "Snippod's Home"))
        ), 
        navLinks
      )
      /* jshint ignore:end */
    );
  },

  handleLogout: function(e) {
    e.preventDefault();
    userActions.logout();
  }

});

module.exports = NavBar;
