'use strict';

var React = require('react'),
    Reflux = require('reflux'),
    DocumentTitle = require('react-document-title'),
    $ = require('jquery'),
    userActions = require('../../actions/userActions'),
    Authentication = require('../../utils/authentication') ;


var Settings = React.createClass({displayName: "Settings",

  mixins: [Authentication],

  render: function() {
    var user = this.props.user;
    var profileActionUrl = '/accounts/'+user.id+'/?_method=PATCH',
        passwordSetActionUrl = '/accounts/'+user.id+'/set_password/?_method=PATCH',
        deleteAccountActionUrl = '/accounts/'+user.id+'/?_method=DELETE';

    return (
      /* jshint ignore:start */
      React.createElement(DocumentTitle, {title: "Settings page"}, 
        React.createElement("div", {className: "settings content full-width"}, 

          React.createElement("h3", null, "Profile Information"), 

          React.createElement("form", {id: "profile-form", action: profileActionUrl, method: "post", onSubmit: this.handleSettings}, 

            React.createElement("p", null, 
              React.createElement("label", {htmlFor: "email"}, "Email:"), 
              React.createElement("input", {type: "text", name: "email", id: "email", defaultValue: user.email})
            ), 

            React.createElement("p", null, 
              React.createElement("label", {htmlFor: "username"}, "Username:"), 
              React.createElement("input", {type: "text", name: "username", id: "username", defaultValue: user.username})
            ), 

            React.createElement("p", null, 
              React.createElement("label", {htmlFor: "first_name"}, "First Name:"), 
              React.createElement("input", {type: "text", name: "first_name", id: "first_name", defaultValue: user.first_name})
            ), 

            React.createElement("p", null, 
              React.createElement("label", {htmlFor: "last_name"}, "Last Name:"), 
              React.createElement("input", {type: "text", name: "last_name", id: "last_name", defaultValue: user.last_name})
            ), 

            React.createElement("button", null, "Update Profile")
          ), 

          React.createElement("h3", null, "Change Password"), 

          React.createElement("form", {id: "password-form", action: passwordSetActionUrl, method: "post", onSubmit: this.handlePassword}, 

            React.createElement("p", null, 
              React.createElement("label", {htmlFor: "password"}, "New Password:"), 
              React.createElement("input", {type: "password", name: "password", id: "password", defaultValue: ""})
            ), 

            React.createElement("p", null, 
              React.createElement("label", {htmlFor: "confirm_password"}, "Confirm Password:"), 
              React.createElement("input", {type: "password", name: "confirm_password", id: "confirm_password", defaultValue: ""})
            ), 

            React.createElement("button", null, "Change Password")
          ), 

          React.createElement("h3", null, "Delete Account"), 

          React.createElement("p", null, "You can delete your account, but keep in mind this action is irreversible."), 

          React.createElement("form", {id: "delete-form", action: deleteAccountActionUrl, method: "post", onSubmit: this.handleDestroy}, 
            React.createElement("button", null, "Delete my account")
          )
        )
      )
      /* jshint ignore:end */
    );
  },

  handleSettings: function(e) {
    e.preventDefault();
    var form = e.currentTarget;

    userActions.updateSettings(form);
  },

  handlePassword: function(e) {
    e.preventDefault();
    var form = e.currentTarget;

    userActions.updatePassword(form);
  },

  handleDestroy: function(e) {
    e.preventDefault();
    var form = e.currentTarget;
    userActions.destroy(form);
  }

});

module.exports = Settings;
