'use strict';

var React = require('react'),
    Reflux = require('reflux'),
    //components
    Post = require('./post/post.jsx'),
    Spinner = require('../modules/Spinner.jsx'),
    //store
    postsStore = require('../../stores/postsStore'),
    //actions
    postActions = require('../../actions/postActions');


var Posts = React.createClass({displayName: "Posts",

  mixins: [
    Reflux.listenTo(postsStore, 'onStoreUpdate'),
    Reflux.listenTo(postActions.updateSortBy, 'updateSortBy')
  ],

  getInitialState: function() {
    var postsData = postsStore.getDefaultData();
    return {
      loading: true,
      posts: postsData.posts,
      sortOptions: postsData.sortOptions,
      nextPage: postsData.nextPage,
      currentPage: postsData.currentPage
    };
  },

  onStoreUpdate: function(postsData) {
    if (!postsData.posts.length) {
      // if no posts are returned
      this.transitionTo('app');
    }
    this.setState({
      loading: false,
      posts: postsData.posts,
      sortOptions: postsData.sortOptions,
      nextPage: postsData.nextPage,
      currentPage: postsData.currentPage
    });
  },

  updateSortBy: function(e) {
    e.preventDefault();

    var currentPage = this.state.currentPage || 1;

    postActions.setSortBy(this.refs.sortBy.getDOMNode().value);

    this.setState({
      loading: true
    });

    if (currentPage === 1) {
      postActions.stopListeningToPosts();
      postActions.listenToPosts(currentPage);
    } else {
      this.transitionTo('posts', { pageNum: 1 });
    }
  },

  render: function() {
    var posts = this.state.posts;
    var currentPage = this.state.currentPage || 1;
    var sortOptions = this.state.sortOptions;
    // possible sort values (defined in postsStore)
    var sortValues = Object.keys(sortOptions.values);
    var user = this.props.user;

    posts = posts.map(function(post) {
      /* jshint ignore:start */
      return React.createElement(Post, {post:  post, user:  user, key:  post.id});
      /* jshint ignore:end */
    });

    var options = sortValues.map(function(optionText, i) {
      /* jshint ignore:start */
      return React.createElement("option", {value:  sortOptions[i], key:  i },  optionText );
      /* jshint ignore:end */
    });

    return (
      /* jshint ignore:start */
      React.createElement("div", {className: "content full-width"}, 
        React.createElement("label", {htmlFor: "sortby-select", className: "sortby-label"}, "Sort by "), 
        React.createElement("div", {className: "sortby"}, 
          React.createElement("select", {
            id: "sortby-select", 
            className: "sortby-select", 
            onChange:  this.updateSortBy, 
            value:  sortOptions.currentValue, 
            ref: "sortBy"
          }, 
             options 
          )
        ), 
        React.createElement("hr", null), 
        React.createElement("div", {className: "posts"}, 
           this.state.loading ? React.createElement(Spinner, null) : posts
        ), 
        React.createElement("hr", null), 
        React.createElement("nav", {className: "pagination"}, 
          
            this.state.nextPage ? (
              React.createElement("span", {to: "posts", params: { pageNum: currentPage + 1}, className: "next-page"}, 
                "Load More Posts"
              )
            ) : 'No More Posts'
          
        )
      )
      /* jshint ignore:end */

    );
  }

});

module.exports = Posts;
