'use strict';

var React = require('react'),
    // actions
    postActions = require('../../../actions/postActions'),
    // components
    Upvote = require('./upvote.jsx');

var Post = React.createClass({displayName: "Post",

  mixins: [
    require('../../mixins/pluralize'),
    require('../../mixins/abbreviateNumber'),
    require('../../mixins/hostnameFromUrl'),
    require('../../mixins/timeAgo')
  ],

  render: function() {
    var user = this.props.user;
    var post = this.props.post;
    var commentCount = post.commentCount || 0;
    var deleteOption = '';

    if (post.isDeleted) {
      // post doesn't exist
      return (
        /* jshint ignore:start */
        React.createElement("div", {className: "post cf"}, 
          React.createElement("div", {className: "post-link"}, 
            "[deleted]"
          )
        )
        /* jshint ignore:end */
      );
    }

    // add delete option if creator is logged in
    if (user.uid === post.creatorUID) {
      deleteOption = (
        React.createElement("span", {className: "delete post-info-item"}, 
          React.createElement("a", {onClick:  postActions.deletePost.bind(this, post.id) }, "delete")
        )
      );
    }

    var upvoteActions = {
      upvote: postActions.upvotePost,
      downvote: postActions.downvotePost
    };

    return (
      /* jshint ignore:start */
      React.createElement("div", {className: "post"}, 
        React.createElement("div", {className: "post-link"}, 
          React.createElement("a", {className: "post-title", href:  post.url},  post.title), 
          React.createElement("span", {className: "hostname"}, 
            "(", React.createElement("a", {href:  post.url},  this.hostnameFromUrl(post.url) ), ")"
          )
        ), 
        React.createElement("div", {className: "post-info"}, 
          React.createElement("div", {className: "posted-by"}, 
            React.createElement(Upvote, {
              upvoteActions:  upvoteActions, 
              user:  user, 
              itemId:  post.id, 
              upvotes:  post.upvotes ? this.abbreviateNumber(post.upvotes) : 0}), 
            React.createElement("span", {className: "post-info-item"}, 
              React.createElement("span", {to: "profile", params: { username: post.creator}},  post.creator)
            ), 
            React.createElement("span", {className: "post-info-item"}, 
                             this.timeAgo(post.time) 
            ), 
            React.createElement("span", {className: "post-info-item"}, 
              React.createElement("span", {to: "post", params: { postId: post.id}}, 
                 this.pluralize(commentCount, 'comment') 
              )
            ), 
             deleteOption 
          )
        )
      )
      /* jshint ignore:end */
    );
  }

});

module.exports = Post;
