/**
*   Topic Component Description
*/

'use strict';

var React = require('react'),
    Reflux = require('reflux'),
    DocumentTitle = require('react-document-title'),
    Router = require('react-router'),

    //components
    TopicCard = require('./topicCard.jsx'),
    Posts = require('./Posts.jsx'),
    ComposerBox = require('./ComposerBox.jsx'),
    //actions
    postActions = require('../../actions/postActions');

var Topic = React.createClass({displayName: "Topic",

  mixins: [
    Router.Navigation,
  ],

  statics: {
    willTransitionTo: function(transition, params) {
      postActions.listenToPosts(+params.pageNum || 1);
    },
    willTransitionFrom: function() {
      postActions.stopListeningToPosts();
    }
  },

  render: function() {
    return (
      /* jshint ignore:start */
      React.createElement(DocumentTitle, {title: "Topic title"}, 
        React.createElement("div", {className: "topic main-container content full-width"}, 
          React.createElement(TopicCard, null), 
          React.createElement(ComposerBox, React.__spread({},  this.props)), 
          React.createElement("h3", null, "And posts list"), 
          React.createElement(Posts, React.__spread({},  this.props))
        )
      )
      /* jshint ignore:end */
    );
  }

});

module.exports = Topic;
